import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { CategoriesService } from '../../modules/categories/categories.service';
import { Router } from '@angular/router';


@Component({
    selector: 'meedias-redactor-category',
	templateUrl: './category.template.html',
	styleUrls: ['./category.styles.scss']
})
export class CategoryComponent implements OnInit {

	constructor(
		private categoryService: CategoriesService,
		private router: Router
	){}

	@Output() categoryDeleted = new EventEmitter<string>();
	@Input() loadedCategory;
	category;
	subcategories = [];
	loaded: boolean;
	detail: boolean;

	async ngOnInit(){
		this.detail = false;
		this.loaded = false;
        await this.categoryService.getCategory(this.loadedCategory).then((data) => {
			this.category = data;		
			console.log(this.category);
			if(this.category.sub.length == 0){
				this.loaded = true;
			}
		})
		
        if(this.category.sub.length != 0){
			for (let index = 0; index < this.category.sub.length; index++) {
				const element = this.category.sub[index];
				this.categoryService.getCategory(element).then(data => {
					this.subcategories.push(data);
					if( index+1 == this.category.sub.length){
						this.loaded = true;
					}
				});
			}
		}
	}

	toggleDetail(){
		this.detail = (this.detail == true) ? false : true;
	}

	deleteCategory(id){
		//TODO a modal i think would be nice here
		this.loaded = false;
		this.categoryService.deleteCategory(id).then(() => {
			this.categoryDeleted.emit(id);
		});
	}

	editCategory(id){
		let url = '/redactor/categories/edit/'+ id;
		console.log(url);
		this.router.navigate([url]);
		
	}

}
